html, body {
  width: 100%;
  height: 100%;
  margin: 0; }

body {
  position: relative;
  background: pink; }

img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 55%;
  height: 20%;
  margin-left: -27.5%;
  margin-top: -11%; }

@media (min-width: 750px) {
  img {
    width: 50%;
    height: 25%;
    margin-left: -25%;
    margin-top: -12.5%; } }

@media (min-width: 950px) {
  img {
    width: 35%;
    height: 35%;
    margin-left: -17.5%;
    margin-top: -11%; } }
